import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import { inject, observer } from "mobx-react";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import CatalogItem from "components/Catalog/CatalogItem";
import Filters from "components/Catalog/filters/Filters";
import CatalogItemSceleton from "components/Catalog/CatalogItemSceleton";
import DynamicFiltersPane from "components/public/DynamicFiltersPane";
import { Paginator } from "primereact/paginator";


const sceletons = <>{Array(24).fill(<CatalogItemSceleton/>)}</>

const Catalog = ({ catalogStore }) => {

    const containerRef = useRef(null);

    useEffect(() => {
        if (!catalogStore.apartments.length)
            catalogStore.loadApartmentsFromAPI();
    }, [ catalogStore, catalogStore.apartments.length ])
    return (
        <Container ref={containerRef}>
            <h2>–û—Ä–µ–Ω–¥–∞ –∫–≤–∞—Ä—Ç–∏—Ä –±–µ–∑ –ø–æ—Å–µ—Ä–µ–¥–Ω–∏–∫—ñ–≤</h2>
            <Content>
                <DynamicFiltersPane>
                    <Filters loadApartments={catalogStore.loadApartmentsFromAPI}/>
                </DynamicFiltersPane>
                <ApartmentsContainer>
                    {catalogStore.state === 'loading' && sceletons}
                    {catalogStore.state === 'ready' && catalogStore.apartments && catalogStore.apartments.length === 0 && <h1>–°—Ö–æ–∂–µ, —â–æ –∑–∞ –≤–∞—à–∏–º –∑–∞–ø–∏—Ç–æ–º –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ –∂–æ–¥–Ω–æ—ó –∫–≤–∞—Ä—Ç–∏—Ä–∏ :(</h1>}
                    {catalogStore.state === 'ready' && catalogStore.apartments && catalogStore.apartments.map(el => <CatalogItem key={el._id} apartment={el}/>)}
                </ApartmentsContainer>
                <span/>
                {catalogStore.apartmentsCount > catalogStore.limit && catalogStore.state === 'ready' && <PaginatorContainer>
                    <Paginator
                        first={catalogStore.offset}
                        rows={catalogStore.limit}
                        totalRecords={catalogStore.apartmentsCount}
                        rowsPerPageOptions={[12, 24, 36]}
                        onPageChange={e => {
                            window.scrollTo(0,0);
                            catalogStore.setPageData(e);
                        }}
                    />
                </PaginatorContainer>
                }
            </Content>
        </Container>
    )
}

export default inject('catalogStore')(observer(Catalog));

const Container = styled.div`
  max-width: calc(${props => props.theme.consts.containerWidth} + 400px);
  margin: auto;
  padding: 20px;
`;

const Content = styled.div`
  display: grid;
  
  @media(min-width: 987px) {
    grid-template-columns: 400px 1fr;
  }
`;

const ApartmentsContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  h1 {
    width: 90%;
  }
`;

const PaginatorContainer = styled.div`
  margin: 20px 0;
`;