import React from 'react';
import { Card } from "primereact/card";
import { Link } from "react-router-dom";
import { Carousel } from 'react-responsive-carousel';
import { routes } from "consts/routes";
import styled from 'styled-components';
import { getMetroSym } from "utils/metro";
import {inject, observer} from "mobx-react";

const Photos = ({data, apartmentId}) => (<Carousel showIndicators={false} showStatus={false} showThumbs={false} infiniteLoop swipeable>
    {data.map(el => {
        const url = el.formats ? el.formats.small ? el.formats.small.url : el.url : el.url;
        return (
            <Link to={routes.APARTMENT + apartmentId} key={el._id}>
                <CarouselContent>
                    <BackgroundImage src={process.env.REACT_APP_DOMAIN_URL + url} />
                    <CarouselImage src={process.env.REACT_APP_DOMAIN_URL + url} />
                </CarouselContent>
            </Link>
        )
    })}
</Carousel>)

const CatalogItem = ({ apartment, userStore, uiStore }) => {
    const { _id, price, photos, currency, moderated, metro, address } = apartment;

    return (
        <ContentCard header={<Photos data={photos} apartmentId={_id}/>}>
            {moderated && <VerifiedMark title="–ü–µ—Ä–µ–≤—ñ—Ä–µ–Ω–∞ –∫–≤–∞—Ä—Ç–∏—Ä–∞" />}
            <Link to={routes.APARTMENT + _id}>
                <Content>
                    <p><b>{price}</b> {currency} / –º—ñ—Å.</p>
                    <div role="button" onClick={e => {
                        e.preventDefault();
                        if (userStore.isLogged) {
                            userStore.user.addLikedApartment(_id)
                        } else uiStore.openLoginModal();
                    }}><i className={`bx bx${userStore.user.liked_apartments.includes(_id) ? 's':''}-heart`}/></div>
                    <p className="small">{address || '–ê–¥—Ä–µ—Å—É –Ω–µ –≤–∫–∞–∑–∞–ª–∏ üòî'}</p>
                    <span/>
                    {metro && <p className="small metro"><span>{getMetroSym(metro.color)}</span> {metro.name}</p>}
                </Content>
            </Link>
        </ContentCard>
    )
}

export default inject('userStore', 'uiStore')(observer(CatalogItem));

const ContentCard = styled(Card)`
  max-width: 250px;
  max-height: 400px;
  position: relative;
  .carousel .slide {
    background: none;
  }
  a {
    text-decoration: none !important;
  }
  &:hover {
    box-shadow: 0 0 4px 4px #ccc;
  }
`;

const CarouselImage = styled.img`
  object-fit: contain;
  height: 200px;
  position: absolute;
  z-index: 1;  
  transform: translate3d(-50%, 0, 0);

`;

const BackgroundImage = styled.img`
  position: absolute;
  top: 0;
  left: -100%;
  filter: blur(2px);
  width: 100%;
  height: 100%;
  z-index: 0;
  transform: translate3d(50%, 0, 0);

`;

const CarouselContent = styled.div`
  min-width: 250px;
  position: relative;
  height: 200px;
`;

const Content = styled.div`
  display: grid;
  grid-template-columns: 1fr 40px;
  grid-auto-rows: auto;
  color: ${props => props.theme.colors.text.important};
  p {margin: 5px;}
  b {font-weight: 700;}
  i {
    font-size: 1.1rem;
    cursor: pointer;
  }
  .small {font-size: .8rem;}
  .metro span {line-height: 1.2rem}
  div[role="button"] {
    display: flex;
    align-items: center;
    justify-content: center;
  }
`;

const VerifiedMark = styled.span`
  background: #2ecf13;
  height: 12px;
  width: 12px;
  display: block;
  border-radius: 50%;
  position: absolute;
  z-index: 1000;
  right: 10px;
  top: 180px;
`;