import React from 'react';
import styled from "styled-components";
import { inject, observer } from "mobx-react";
import { Sidebar } from "primereact/sidebar";
import { Card } from "primereact/card";

const DynamicFiltersPane = ({children, uiStore}) => {
    return (
        <Container>
            <Card className="lg">{children}</Card>
            <div className="sm">
                <FloatingButton onClick={uiStore.toggleFilters}>
                    <i className='bx bx-filter'/>
                </FloatingButton>
                <SidebarPanel visible={uiStore.isFiltersVisible} onHide={uiStore.toggleFilters}>
                    {children}
                </SidebarPanel>
            </div>
        </Container>
    )
}

export default inject('uiStore')(observer(DynamicFiltersPane));

const Container = styled.div`
  @media(min-width: 987px) {
    .lg { display: block; }
    .sm { visibility: hidden }
  }

  @media(max-width: 987px) {
    .lg { display: none; }
    .sm { visibility: visible }
  }
`;

const SidebarPanel = styled(Sidebar)`
  z-index: 1000;
  width: max-content;
  max-width: 100%;
  overflow: auto;
`;

const FloatingButton = styled.button`
  background: white;
  font-size: 3rem;
  line-height: .8;
  padding: 0.2rem;
  color: ${props => props.theme.colors.text.general};
  box-shadow: 0 0 2px 2px #ccc;
  cursor: pointer;
  position: fixed;
  bottom: 30px;
  left: 30px;
  border: none;
  border-radius: 50%;
  z-index: 5;

  &:hover {
    background: #fafafa;
    box-shadow: 0 0 4px 4px #ccc;
  }
`;