import React, { useState } from 'react';
import { AutoComplete } from "primereact/autocomplete";
import { getMetroSym } from "utils/metro";
import styled from 'styled-components';
import { inject, observer } from "mobx-react";
import { SNIPPETS } from "consts/styles";

const selectedRegionTemplate = (option, props) => {
    if (option) return (
        <span className="p-autocomplete-token-label">
            {getMetroSym(option.color)} {option.name}
        </span>
    );
    return (<span>{props.placeholder}</span>);
}

const regionTemplate = option => (<span>{option.name}</span>)

const Regions = ({ filtersMW, uiStore }) => {

    const [ filteredRegions, setFilteredRegions ] = useState(uiStore.metros);

    const searchRegion = e => {
        setFilteredRegions(uiStore.regions.filter(region =>
            region.name.toLowerCase().includes(e.query.trim().toLowerCase()))
        );
    }

    return (
        <SelectRegions
            className="region-input"
            value={filtersMW.regions}
            suggestions={filteredRegions}
            completeMethod={searchRegion}
            field="name"
            placeholder="–†–∞–π–æ–Ω"
            multiple
            dropdown
            selectedItemTemplate={selectedRegionTemplate}
            itemTemplate={regionTemplate}
            onChange={filtersMW.setRegions}
        />
    )
}

export default inject('filtersMW', 'uiStore')(observer(Regions));

const SelectRegions = styled(AutoComplete)`
    width: 100%;
    margin-bottom: 10px;
    font-size: .8rem !important;
    ul {
      ${ SNIPPETS.noOutline };
      box-shadow: none !important;
      width: 100%;
      li {
        font-size: .8rem !important;
      }
    }
    input {
    font-size: .8rem !important;
    }
    .p-autocomplete-token {
      margin-bottom: 3px;
    }
    
    .p-placeholder {
    font-size: 1.2rem !important;
    }
    button {
    background: white;
    border-right: 1px solid #ced4da;
    border-top: 1px solid #ced4da;
    border-bottom: 1px solid #ced4da;
    border-left: none;
    }
`;